import * as types from './types';

//可以直接用（axios中有window.axios=axios;），也可以直接引进来
// import axios from 'axios';

export default{
	[types.VIEW_LOADING]:({state,commit},payload)=>{
	//校验payload的类型
	commit(types.VIEW_LOADING,payload)
	
	},
	[types.VIEW_NAV]:({state,commit},payload)=>{
		// console.log('actions',payload)
	//校验payload的类型
	commit(types.VIEW_NAV,payload)
	
	},
	[types.VIEW_FOOT]:({state,commit},payload)=>{
	//校验payload的类型
	commit(types.VIEW_FOOT,payload)
	
	},
	[types.VIEW_SEND]:({state,commit},payload)=>{
	//校验payload的类型
	commit(types.VIEW_SEND,payload)
	
	},
	[types.UPDATE_BANNER]:({state,commit},payload)=>{
	//校验payload的类型
	axios({
		url:'/api/banner',
		params:{_pages:1,_limit:5}
	}).then(
		res=>commit(types.UPDATE_BANNER,res.data.data)
		)
	},
	[types.UPDATE_HOME]:({state,commit},payload)=>{
	axios({
		url:'/api/home',
		params:{_pages:1,_limit:10}
	}).then(
		res=>commit(types.UPDATE_HOME,res.data.data)
		)
	},
	[types.UPDATE_SERVICE]:({state,commit},payload)=>{
	//校验payload的类型
	axios({
		url:'/api/service',
		params:{_pages:1,_limit:10}
	}).then(
		res=>commit(types.UPDATE_SERVICE,res.data.data)
		)
	},
	[types.UPDATE_STORE]:({state,commit},payload)=>{
	//校验payload的类型
	axios({
		url:'/api/store',
		params:{_pages:1,_limit:10}
	}).then(
		res=>commit(types.UPDATE_STORE,res.data.data)
		)
	},
	[types.UPDATE_DETAIL]:({state,commit},{dataName,id})=>{
	//校验payload的类型
	axios({
		url:`/api/${dataName}/${id}`,
	}).then(
		res=>commit(types.UPDATE_DETAIL,res.data.data)
		)
	},
	[types.CHECK_USER]:({state,commit},{username,password})=>{
	//校验payload的类型
	return axios({
	  url:'/api/login',
	  method:'post',
	  // withCredentials:true,//追条携带
	  data:{
	    username,password,
	    save:true
	  }
	}).then(
	//此时成功返回成功的数据，失败返回失败的数据
		res=>{
			//登录时将数据存到cookie的同时存到localStorage中，避免强刷又要重新登录
			commit(types.CHECK_USER,res.data);
			window.localStorage.setItem('user',JSON.stringify(res.data));
			return {
				mess:res.data.msg,
				err:res.data.err
			}
		}
	  )
   },
   
   //购物车部分
   
   //添加购物车
   addItem:({commit,state},payload)=>{//state是复制的，修改不影响真实state
		//将要修改的state放到新的数组里
		// console.log(payload)
		// console.log(id,count)
		if(!payload.count) payload.count = 1
		// console.log(payload)
		let arr = [...state.buycar];
		let find = false;
		arr.forEach((item,index)=>{
			if(item._id==payload._id){
				console.log(item._id,payload._id)
				item.count++;//数量递增
				find=true;
			}
		});
		if(!find){
			// payload.count=1;//数量
			arr.push(payload);
		}
		console.log(arr)
		// commit(type.ADD_ITEM,[...state.buycar])
		commit(types.ADD_ITEM,arr)
   },
   
   //更改购物车
   changeItem:({commit,state},payload)=>{
	   //[...state.buycar]
	   //[...state]
	   state.buycar.forEach((item,index)=>{
		   if(item._id==payload._id){
			   item.count += payload.count;
		   }
	   });
	   //如果是对象，需要复制一份，不然state.buycar指向的是复制后state修改之前的引用
	   commit(types.CHANGE_ITEM,[...state.buycar]);
	   //commit(types.CHANGE_ITEM,state)不能这么写
   },
   
   //删除购物车里面的一项
   removeItem:({commit,state},payload)=>{
	   state.buycar.forEach((item,index)=>{
		   if(item._id==payload._id){
			   state.buycar.splice(index,1);
		   }
	   });
	   commit(types.REMOVE_ITEM,[...state.buycar]);
	   //commit(types.REMOVE_ITEM,state.buycar)
   },
   
   //清空购物车
   clearBuycar:({commit,state})=>{
	   commit(types.CLEAR_BUYCAR,[])
   }
	
	
}