<script>
import store from '../plugins/store.js'
export default {
  /* data(){
    return {
      user:{}
    }
  }, */
  beforeRouteEnter(to,from,next){
	  //							跳过来传给目标组件
   // store.state.$store.state.user.err == 0 ? next(_this=>_this.user=store.state.user):next('/login')
  //直接找状态管理拿
  store.state.user.err == 0 ? next():next('/login')
  },
  methods:{
    logout(){
      axios({
        url:'/api/logout',
        method:'delete'
      }).then(
        res=>{
			if(res.data.err===0){this.$router.push('/home')
			//清除cookie，防止注销后用户的登录信息依然存在
			this.$store.commit('CHECK_USER',{
				err:1,
				msg:'未登录'
				})
				//未登录清除localStorage
				window.localStorage.removeItem('user')
			}
		}
	  )
    }
  }
}
</script>

