<template>
  <div class="detail" v-if="$store.state.detail._id" >
		<div  class="pro_tab clearfix">
		    <span><a href="#" @click="goback">返回&lt;-</a></span>
		    <span><a href="#" class="aon">图片详情</a></span>
		</div>
		<div class="proban">
		    <div id="slideBox" class="slideBox">
		        <swipe class="bd my-swipe" :speed="1000" >
		                <swipe-item class="slide1" ><img :src="server.baseUrl+$store.state.detail.detail.slide1" /></swipe-item>
		            	<swipe-item class="slide2" ><img :src="server.baseUrl+$store.state.detail.detail.slide2" /></swipe-item>
		            	<swipe-item class="slide3" ><img :src="server.baseUrl+$store.state.detail.detail.slide3" /></swipe-item>
						<swipe-item class="slide3" ><img :src="server.baseUrl+$store.state.detail.detail.slide4" /></swipe-item>
		        </swipe>
		        <div class="hd"><ul></ul><span class="pageState"></span></div>
		    </div>
		</div>
		<div class="pro_header">
		    <div class="pro_tips">
		        <h2>{{$store.state.detail.detail.title2}}</h2>
		        <h3><b>￥{{$store.state.detail.detail.money }}</b><font>{{$store.state.detail.detail.num}}件</font>起批</h3>
		    </div>
		    <span class="share_span"><i class="share_icon"></i><b>分享商品</b></span>
		</div> 
		<div class="pro_express">{{$store.state.detail.detail.express}}<span>{{$store.state.detail.detail.address}}</span><b>{{$store.state.detail.detail.sold}}</b></div>
		<div class="pro_virtue">
		    <div class="pro_vlist"><b>颜色</b><span>黑色</span><span class="aon">紫色</span></div>
		    <div class="pro_vlist"><b>尺码</b><span>S</span><span>M</span><span class="aon">L</span></div>
		    <div class="pro_vlist">
		        <b>数量</b>
		        <div class="quantity-form">
		            <a class="icon_lower" @click="jian"></a>
		            <div class="input_quantity" readonly="readonly" max="10">{{count}}</div>
					<!-- <input type="text" class="input_quantity" value="1" readonly="readonly" max="10" v-model="count" /> -->
		            <a class="icon_plus" @click="jia"></a>
		        </div>
		    </div>
		</div>
		<div class="pro_warp">
		    <p>{{$store.state.detail.detail.content}}</p>
		    <img :src="server.baseUrl+$store.state.detail.detail.img1" />
		    <img :src="server.baseUrl+$store.state.detail.detail.img2" />
		    <img :src="server.baseUrl+$store.state.detail.detail.img3" />
		</div>
		<div class="pro_fixed clearfix">
		    <router-link to='/store' tag="a"><i class="sto_icon"></i><span>店铺</span></router-link>
		    <router-link to='/service' tag="a"><i class="custo_icon"></i><span>客服</span></router-link>
		    <router-link to='/buycar' tag="a"><i class="keep_icon"></i><span>购物车</span></router-link>
		    <input type="button" value="立即订购" class="order_now_btn" />
		    <input type="button" value="加入购物车" class="add_cart_btn" @click="addItem($store.state.detail)"/>
		</div>
		
  </div>
</template>
<script>
import 'vue-swipe/dist/vue-swipe.css'
import { Swipe, SwipeItem } from 'vue-swipe';
import {mapState,mapActions} from 'vuex';
import {UPDATE_DETAIL} from '../store/types.js'

	
// import server from '../config/server.js'
export default {
	template:'#detail',
	props:['banner'],
	components: {
		  'swipe': Swipe,
		  'swipe-item': SwipeItem
	  },
  data(){
    return {
		  item:{},
		  count:1,
    }
  },
  computed:mapState(['detail']),
    props:{
      id:String,
      dataName:String,
      
    },
 
  methods:{
	  ...mapActions(['addItem']),
	  goback(){
	    this.$router.go(-1)
	  },
  	  jia(){
  		  this.count++;
  		  // console.log(this.$stroe.state.detail)
		  this.$store.state.detail.count= this.count;
		  // console.log(this.dataName)
  		  //components (通过dispatch)- >  actions
  		  // this.$store.dispatch('类型',参数/负载/payload)  -> actions
  		  // this.$store.dispatch('jia',1)
  	  }, 
  	//另一种写法
  	  jian(){
		  this.count--;
		  // this.item.number= this.count;
  	  		  //components (通过dispatch)- >  actions
  	  		  // this.$store.dispatch('类型',参数/负载/payload)  -> actions
  	  		  // this.$store.dispatch({type:'jian',payload:13})
  	  		  
  	  },
	  
	},
	activated(){
		let id = this.$route.params.id;
		let dataName = this.$route.query.dataName;
		//这样写的目的是为了让除了detail里面有内容，其它的均为空
		this.$store.dispatch('UPDATE_DETAIL',{id,dataName})
		
	},
}

</script>

<style scoped>
.pro_tab{background:#fff;padding:.1rem 0;}
.pro_tab span{float:left;width:50%; border-right:1px solid #ccc;text-align:center;}
.pro_tab span:last-child{border-right:0;}
.pro_tab span a{color:#454545; padding:0 10px;}
.pro_tab span a.aon{border-bottom:3px solid #f20c11; padding-bottom:2px;}
.proban .slideBox .hd ul{display:none;}
.proban .slideBox .hd{text-align:right;}
.proban .slideBox .pageState{position:absolute; right:10px;top:0; color:#fff;}
/* .slideBox .bd { position: relative; z-index: 0; }
.slideBox .bd li { position: relative; text-align: center; } */
.proban .slideBox .bd  img { width: 100%; }


.pro_header{background:#fff;position:relative;padding:.1rem; padding-right:80px;}
.pro_header h2{margin:0; padding:0;color:#666; font-size:.18rem; font-weight:normal; line-height:22px;}
.pro_header h3{margin:0; padding:5px 0 0 0;color:#878787; font-size:.16rem; font-weight:normal;}
.pro_header h3 b{color:#f20c11;font-size:.18rem;}
.pro_header h3 font{display:inline-block; padding-left:10px; padding-right:10px;}
.pro_header .pro_tips{border-right:1px solid #ccc;}
.pro_header .share_span{display:inline-block;text-align:center;color:#bfbfbf; font-size:.16rem;position:absolute;right:0;top:50%; width:80px;transform: translateY(-50%); -moz-transform: translateY(-50%); -ms-transform: translateY(-50%); -webkit-transform: translateY(-50%);}
.pro_header .share_span b{text-align:center;display:block; font-weight:normal; line-height:1.6px;}
.share_icon{display:inline-block;width:.3rem; height:.3rem;background:url(../assets/img/fenxiang_403x.png) no-repeat left top;background-size:contain;}


.pro_express{background:#fff; padding:.1rem; margin-top:.1rem; position:relative;color:#878787;}
.pro_express span{position:absolute;right:.1rem;}
.pro_express b{font-weight:normal;position:absolute; left:50%;transform: translateX(-50%); -moz-transform: translateX(-50%); -ms-transform: translateX(-50%); -webkit-transform: translateX(-50%);}

.pro_virtue{background:#fff;margin-top:.1rem;padding:1rem;}
.pro_vlist{position:relative;padding:.1rem 0 0 .1rem; padding-left:50px;}
.pro_vlist b{display:inline-block; line-height:34px; position:absolute;left:0;color:#454545; font-weight:normal;}
.pro_vlist span{display:inline-block; border:1px solid #ccc; line-height:34px; padding:0 .14rem; margin-right:5px; margin-bottom:5px;}
.pro_vlist span.aon{color:#f20c11;}

.pro_warp{padding:.1rem; background:#fff; margin-top:.1rem; padding-bottom:80px;}
.pro_warp p{padding-bottom:0.5rem;}
.pro_warp img{max-width:100%;}


.pro_fixed{background:#fff;width:100%; left:0; bottom:0; position:fixed; z-index:100; border-top:1px solid #e6e6e6;}
.pro_fixed a{display:block;float:left;width:15%;text-align:center;padding:.35rem  0 0.05rem  0; position:relative;}
.pro_fixed a span{display:block; text-align:center;font-size:.14rem;}
.pro_fixed a i{display:block; position:absolute; left:50%;top:.1rem;width: .23rem;height: .2rem;}
.sto_icon{width:.23rem;height:.2em;margin-left:-.12rem; background:url(../assets/img/dianpu_403x.png) no-repeat left top;background-size:contain;}
.custo_icon{width:.21rem;height:.2em;margin-left:-.1rem; background:url(../assets/img/kefu_403x.png) no-repeat left top;background-size:contain;}
.keep_icon{width:.2rem;height:.2em;margin-left:-.1rem; background:url(../assets/img/shoucang_403x.png) no-repeat left top;background-size:contain;}
.pro_fixed .order_now_btn{background:#f20c11;width:25%; border:0;color:#fff;text-align:center;height:101%;position:absolute;right:0;top:-1px;font-size:.16rem;}
.pro_fixed .add_cart_btn{background:#f2a90c;width:30%; border:0;color:#fff;text-align:center;height:101%;position:absolute;right:25%;top:-1px;font-size:.16rem;}


.quantity-form{display:inline-block;background:#fff;border:1px solid #ccc;  z-index:3;height:28px;white-space:nowrap;width:90px;}
.quantity-form a{display:block; width:27px; height:26px; float:left;cursor:pointer; position:relative;}
.quantity-form .input_quantity{width:32px;height:26px;font-size:.12rem; text-align:center;float:left;background:none; border:0; line-height:26px; color:#5c5c5c;vertical-align:middle; overflow:hidden;}
.quantity-form .icon_lower{border-right:1px solid #ccc;}
.quantity-form .icon_lower:before{content:" ";width:16px;height:1px; background:#989191; position:absolute; left:5px; top:50%;}
.quantity-form .icon_plus{border-left:1px solid #ccc;}
.quantity-form .icon_plus:before{content:" ";width:16px;height:1px; background:#f20c11; position:absolute; left:6px; top:50%;}
.quantity-form .icon_plus:after{content:" ";width:1px;height:16px; background:#f20c11; position:absolute; left:13px; top:6px;}
.patch_op .quantity-form{position:absolute; left:50%;top:3px; transform: translateX(-50%); -moz-transform: translateX(-50%); -ms-transform: translateX(-50%); -webkit-transform: translateX(-50%);}

.my-swipe {height: 400px;width:100%;color: #fff;font-size: 30px;text-align: center;}
.my-swipe img{width:100%;height: 400px;}
.slide1 {height: 400px;color: #fff;}
.slide2 {height: 400px;color: #000;}
.slide3 {height: 400px;color: #fff;}

</style>