<template>
  <div class="banner" id="slideBox">
	<swipe class="my-swipe" :speed="1000">
	    <swipe-item class="slide1" ><img :src="server.baseUrl+data.banner1" alt=""/></swipe-item>
		<swipe-item class="slide2" ><img :src="server.baseUrl+data.banner2" alt=""/></swipe-item>
		<swipe-item class="slide3" ><img :src="server.baseUrl+data.banner3" alt=""/></swipe-item>
		
	</swipe>
		
	
	
 </div>
</template>
<script>

import 'vue-swipe/dist/vue-swipe.css'
import { Swipe, SwipeItem } from 'vue-swipe';


export default {
	template:'#banner',
  props:['banner'],
  components: {
	  'swipe': Swipe,
	  'swipe-item': SwipeItem
    },
	data(){
		return{
			data:{}
		}
	},
	mounted(){
		axios({
			url:'http://localhost:3000/api/banner'
		}).then(
			res=>{
			this.data=res.data.data[0]
				// console.log(res.data.data[0].banner2)
			}
		)
	},
	
} 
</script>

