<template>
  <div id="app" class="box">
    <Loading v-show="$store.state.bLoading"></Loading>
    <Header v-show="$store.state.bNav"></Header>
    <transition
			enter-active-class = "animated bounceInLeft"
    >

      <keep-alive>
      <router-view></router-view>
    </keep-alive>

   </transition>
   <Send v-show="$store.state.bSend"></Send>
    <Footer v-show="$store.state.bFoot"></Footer>
  </div>
</template>

<script>
import Header from './components/Header'
import Footer from './components/Footer'
import Loading from './components/Loading'
import Send from './components/Send'

import { VIEW_NAV,VIEW_FOOT,VIEW_SEND } from './store/types.js'

export default {
  name: 'app',
  components: {
    Header,Footer,Loading,Send
  },
  
/*  mounted(){
	  console.log('store',this.$store)
  }, */
  
  methods:{
    checkPath(path){//路由检测
      // console.log('path',path);
      if(/home|follow/.test(path)){
		  // console.log(this.$store)
		this.$store.dispatch(VIEW_NAV,true)
		this.$store.dispatch(VIEW_FOOT,true)
		this.$store.dispatch(VIEW_SEND,false)
		// this.$root.$data.bNav=true;
		// this.$root.$data.bFoot=true;
		// this.$root.$data.bSend=false;
      }
	  if(/service/.test(path)){
		  console.log('checkPath')
		  this.$store.dispatch(VIEW_NAV,false)
		  this.$store.dispatch(VIEW_FOOT,true)
		  this.$store.dispatch(VIEW_SEND,true)
		// this.$root.$data.bNav=false;
		// this.$root.$data.bSend=true;
		// this.$root.$data.bFoot=true;
	  }
      if(/user|store|buycar/.test(path)){
		  this.$store.dispatch(VIEW_NAV,false)
		  this.$store.dispatch(VIEW_FOOT,true)
		  this.$store.dispatch(VIEW_SEND,false)
		// this.$root.$data.bNav=false;
		// this.$root.$data.bFoot=true;
		// this.$root.$data.bSend=false;
      }
      if(/login|reg|Detail/.test(path)){
		this.$store.dispatch(VIEW_NAV,false)
		this.$store.dispatch(VIEW_FOOT,false)
		this.$store.dispatch(VIEW_SEND,false)
		// this.$root.$data.bNav=false;
		// this.$root.$data.bFoot=false;
		// this.$root.$data.bSend=false;
      }
    }
  },
  watch:{
    $route:{
      handler(current,prev){
        // current.path==/home|/follow|/colukn|/detail/23423234
        this.checkPath(current.path)
      },
      immediate:true,
      deep:true
    }
  }
}
</script>

