<template>
	<div class="order_pro_box buycar">
	    <ul class="order_pro_list">
	        <li
	          v-for="(item,index) of buycar"
	          :key="item._id"
	        >
	            <!-- <i><input id='check-1' type="checkbox" name='check-1' /><label for="check-1">&nbsp;</label></i> -->
				<input type="checkbox" class="checkbox" />
	            <a href="proinfo.html" class="pic"><img :src="server.baseUrl+item.detail.img1" /></a>
	            <h2><a href="proinfo.html">{{item.detail.title2}}</a></h2>
	            <h3>M<span>黑色</span></h3>
	            <div class="order_c_op">
	                <b>{{item.detail.money * $store.state.buycar[index].count | currency('￥',2) }}</b>
	                <span class="delC_icon" @click="removeItem({_id:item._id})"></span>
	                <div class="quantity-form">
						
	                    <a class="icon_lower" @click="changeItem({_id:item._id,count:-1})"></a>
	                    <!-- <input type="text" class="input_quantity" value="1" readonly="readonly" max="10" /> -->
						<div class="input_quantity" readonly="readonly" max="10" >{{$store.state.buycar[index].count}}</div>
	                    <a class="icon_plus" @click="changeItem({_id:item._id,count:1})"></a>
	                </div>
	            </div>
	        </li>
		</ul>
		<div class="cart_fixed">
		    <a class="billing_btn1" @click="clearBuycar" v-show="buycar.length>0">全部清空</a>
			<a href="#" class="billing_btn2">结算</a>
		    <span><input id='check-0' type="checkbox" name='check-1' /><label for="check-0">全选</label></span>
		    <b>合计：<strong>¥</strong><font id="price">118</font></b>
		</div>
	</div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
export default {
	data(){
		return {
			totalPrice:0, //总金额
			checkAllItem:false, //全部选中
			checkedList:[] //选中的数
		}
		 
	},
	computed:mapGetters(['buycar']),
	methods:{
		...mapActions([
			'changeItem','clearBuycar','removeItem'
		]),
		
},
	
	filters:{
	  currency(data,currency,digit){
	    currency=currency||'￥'
	    digit=digit||2
	    return currency + data.toFixed(digit)
	  }
	}
}

</script>

